<template xmlns:v-slot="http://www.w3.org/1999/XSL/Transform">
    <div class="task pageSpace h100">
        <div>
            <a-form :label-width="80">
                <a-row :gutter="[24,24]" style="margin:0">
                    <a-col :xxl="8" :xl="12" style="padding: 0 12px 12px;">
                        <label style="color: white" class="searchCss">分析对象</label>
                        <a-select class="searchContentCss" v-model="objValue.value" filterable remote :max-tag-count="1" @change="objChange" allowClear>
                            <a-select-option v-for="item in objByCurrentUnit" :key="item" :value="item">{{item}}</a-select-option>
                        </a-select>
                    </a-col>
                    <a-col :xxl="8" :xl="12" style="padding: 0 12px 12px;">
                        <label style="color: white;" class="searchCss">开始时间</label>
                        <a-date-picker class="searchContentCss" format="YYYY-MM-DD HH:mm:ss" :show-time="{defaultValue:moment('00:00:00','HH:mm:ss')}" @change="timePickStart"></a-date-picker>
                    </a-col>
                    <a-col :xxl="8" :xl="12" style="padding: 0 12px 12px;">
                        <label style="color: white;" class="searchCss">结束时间</label>
                        <a-date-picker class="searchContentCss" format="YYYY-MM-DD HH:mm:ss" :show-time="{defaultValue:moment('00:00:00','HH:mm:ss')}" @change="timePickEnd"></a-date-picker>
                    </a-col>
                    <a-col :xxl="8" :xl="12" style="padding: 0 12px 12px;">
                        <label style="color: white;" class="searchCss">协议</label>
                        <a-select class="searchContentCss" v-model="agrementValue.value" mode="multiple" filterable remote :max-tag-count="1">
                            <!-- <a-select-option value="mta">SMTP</a-select-option>
                            <a-select-option value="pop3">POP3</a-select-option>
                            <a-select-option value="imap">IMAP</a-select-option>
                            <a-select-option value="wms">HTTP</a-select-option> -->
                            <a-select-option :value="item.code" v-for="item of ruleGroupData" :key="item.id">{{item.name}}</a-select-option>
                        </a-select>
                    </a-col>
                    <a-col :xxl="8" :xl="12" style="padding: 0 12px 12px;">
                        <label style="color: white;" class="searchCss">归属地</label>
                        <a-select  class="searchContentCss" v-model="locationValue.value" filterable remote :max-tag-count="1">
                            <a-select-option value="all">全部</a-select-option>
                            <a-select-option value="inchina">中国</a-select-option>
                            <a-select-option value="nochina">境外</a-select-option>
                            <a-select-option value="norn">非局域网</a-select-option>
                        </a-select>
                    </a-col>
                    <a-col :xxl="8" :xl="12" style="padding: 0 12px 12px;">
                        <label style="color: white" class="searchCss">登录成功邮箱>=</label>
                        <a-input class="searchContentCss" addon-after="次" v-model="successSize.value"></a-input>
                    </a-col>
                </a-row>
                <a-row>

                    <a-col :lg="2" style="float: right">
                        <a-button type="primary" @click="exportCsv()" :loading="exportLoading">导出</a-button>
                    </a-col>
                    <a-col :lg="2" style="float: right">
                        <a-button type="primary" @click="search()">查询</a-button>
                    </a-col>
                </a-row>
            </a-form>
        </div>
        <div style="margin-top: 40px">
            <a-table
                    :columns="columns"
                    rowKey="dataIndex"
                    :pagination="true"
                    :loading="loading"
                    size="default"
                    :dataSource="searchData">
                 <span slot="action" slot-scope="text,record">
                                <a class="t-optColor-success" @click="detailRecords(text,record)">详情</a>
                           </span>
                            <!-- @click="ipClick(record)" style="cursor: pointer;color: #1890ff" -->
                <span slot="req_ip" slot-scope="text,record">
                    <span>{{record.req_ip}}</span>
                </span>
            </a-table>
            
            <!-- --详情模态框--- -->
            <a-modal :title="detailsTitle" v-model="modalVisible" :width="'60%'" class="modalCls">
                <a-table border :columns="deColumns" :dataSource="detailsData" :loading="detailLoading"  size="default" :pagination="{showSizeChanger:true,showQuickJumper:true,pageSizeOptions:sizeListModal}">
                    <!-- @click="emailNameClick(record)" style="cursor: pointer;color: #1890ff" -->
                    <span slot="emailName" slot-scope="text,record" >
                    <span>{{record.emailName}}</span>
                </span>
                </a-table>
                <p>
             <span>
               当前显示:
               <span style="font-size:20px;color:#2D8CF0">{{detailsData.length}}</span>
              </span>
                </p>
                <div slot="footer">
                    <a-button type="primary" v-if="detailsData.length < detailTotal" @click="dataMore()"
                              :disabled='detailLoading'>
                        加载更多
                    </a-button>
                    <span v-else>已显示全部</span>
                </div>
            </a-modal>
            <p style="float: right;margin-left: 64px;margin-top: 16px; font-size: 15px;">总条数：{{total}}</p>
        </div>
        <!-- <div class="ar" style="margin-top:30px" v-if="searchData.length > 0">
            <a-pagination
                    v-model="pageParam.page"
                    :total="total"
                    :page-size="pageParam.size"
                    @change="pageChange"
            ></a-pagination>
         </div> -->
    </div>
</template>

<script>
    import moment from 'moment'
    import {mapState} from 'vuex'
    import { csvHelper } from "../../../../../common/csv-helper";
    export default {
        name: "illegalAnalyze",
        data(){
            return{
                csvHelper,
                loading:false,
                detailsTitle: "详情记录", // 详情模态框标题
                detailLoading: false, // 详情表格加载状态
                detailTotal: 0,
                exportLoading:false,
                // 表格数据
                searchData: [],
                sizeList: ["5", "10", "20", "30"],
                sizeListModal: ["5", "10", "20", "30"],
                total: 0, // 总数
                // 定义开始时间
                starttimes: "",
                // 定义结束时间
                endtimes: "",
                match: {
                    countries: [],
                    objName:[],
                    units:[]
                },
                columns: [
                    {
                        dataIndex: "req_ip",
                        title: "请求IP",
                        scopedSlots:{customRender:'req_ip'}
                    },
                    {
                        dataIndex: "emailCount",
                        title: "邮箱个数",
                        sorter: (a, b) => a.emailCount - b.emailCount
                    },
                    {
                        dataIndex: "country",
                        title: "归属地",
                    },
                    {
                        dataIndex: "action",
                        title: "操作",
                        scopedSlots: { customRender: "action" },
                    },
                ],
                detailsColumns: [
                    // 模态框中表格字段
                    {
                        title: "邮箱名",
                        dataIndex: "emailName",
                        scopedSlots:{customRender:'emailName'},
                        sorter: (a, b) => a.emailName - b.emailName
                    },
                    {
                        title: "成功个数",
                        dataIndex: "successCount",
                        sorter: (a, b) => a.successCount - b.successCount
                    },
                    {
                        title: "失败个数",
                        dataIndex: "failCount",
                        sorter: (a, b) => a.failCount - b.failCount
                    }
                ],
                // 协议类型
                agrementValue: {
                    label: "ptTag",
                    value: []
                },
                // 归属地
                locationValue: {
                    label: 'countryTag',
                    value: "all"
                },
                // 登录成功次数
                successSize: {
                    lable: 'successThreshold',
                    value: "3"
                },
                // 单位
                unitValue:{
                    label:"unit",
                    value:[]
                },
                // 分析对象
                objValue:{
                    label:"ana_obj",
                    value:""
                },
                // 单位id
                unitName:'',
                detailsData: [], // 模态框表格数据源
                modalVisible: false, // 模态框是否可见
                ruleGroupData:[],
                // 分页
                pageParam: {
                    page: 1,
                    size: 10,
                },
            }
        },
        computed: {
            deColumns: function () {
                return this.detailsColumns.map(col => {
                    // console.log(col);
                    return {
                        ...col,
                        className: 'single-line'
                    };
                });
            },
            ...mapState(["selectUnitEn","objByCurrentUnit"]),
        },
        methods:{
            moment,
            // 选择开始时间控件
            timePickStart(time) {
                this.starttimes = new moment(time).format("YYYY-MM-DD HH:mm:ss");
            },
            // 选择结束时间控件
            timePickEnd(time) {
                this.endtimes = new moment(time).format("YYYY-MM-DD HH:mm:ss");
            },
            // 获取表格数据
            getTableData(){
                this.loading = true;
                let params = Object.assign({},{
                    beginTime:this.starttimes,
                    endTime:this.endtimes,
                    units:[sessionStorage.getItem('selectUnitEn')],
                    ana_obj:this.objValue.value,
                    rule_groups:this.agrementValue.value,
                    country:this.locationValue.value,
                    successNumber:this.successSize.value,
                    top:10000,
                    loginType:"login"
                }
                // ,this.pageParam
                );
                this.$API.getUnormalLoginAgg(params).then((res) => {
                    if (res.code === 1000) {
                        this.searchData = res.data;
                        this.total = res.data.length;
                        this.loading = false;
                    } else {
                        this.searchData = [];
                        this.loading = false;
                    }
                });
            },
            pageChange() {
                this.getTableData();
            },
            // 获取单位
            getUnit(){
                this.$API.searchUnit({}).then((res) => {
                    if (res.code === 1000) {
                        this.match.units = res.data;
                        this.unitValue.value = res.data[0].ename;
                        // this.unitChange(this.unitValue.value);
                    } else {
                        this.match.units = [];
                    }
                })
            },
            // 查询按钮
            search() {
                this.page = 1;
                this.getTableData()
            },
            // 获取分析对象
            getAnalysisObj(){
                this.$API.getAnalysisObj({name:this.selectUnitEn}).then((res) => {
                    if (res.code === 1000) {
                        this.match.objName = res.data;
                        this.objValue.value = res.data[0];
                        this.selectByObjectName(this.objValue.value);
                    } else {
                        this.match.countries = '';
                        this.$message.warning('分析对象暂无数据')
                    }
                })
            },
            // 分析对象变化事件
            objChange(){
                this.selectByObjectName(this.objValue.value);
            },
            // 根据分析对象获取分析对象id
            selectByObjectName(objName){
                let params = {
                    name:objName
                };
                this.$API.selectByObjectName(params).then((res) => {
                    if (res.code === 1000) {
                        this.objId = res.data.id;
                        this.getAgreement(this.objId);
                    } else {
                        this.objId = "";
                    }
                });
            },
            /**
             * 详情加载数据
             */
            detailRecords(text,row) {
                this.detailsData = row['emailList'] || [];
                this.modalVisible = true;
            },
            // 跳转到详情
            ipClick(val){
                console.log(val);
                let beginDate = "";
                let endDate = "";
                if (this.starttimes) {
                    beginDate = this.starttimes;
                }
                if (this.endtimes) {
                    endDate = this.endtimes;
                }
                let rule_group_str = "";
                for (let i = 0; i < this.agrementValue.value.length; i++) {
                    rule_group_str += `rizhi=${this.agrementValue.value[i]}&`
                }
                var url = "/#/business/search/searchLog";
                var params = `?&beginDate=${beginDate}&endDate=${endDate}&req_ip=${val.req_ip}&${rule_group_str}unitList=${this.unitValue.value}`;
                window.open(url + params, "_blank");
            },
            // 跳转到详情
            emailNameClick(val) {
                console.log(val);
                let beginDate = "";
                let endDate = "";
                if (this.starttimes) {
                    beginDate = this.starttimes;
                }
                if (this.endtimes) {
                    endDate = this.endtimes;
                }
                let rule_group_str = "";
                for (let i = 0; i < this.agrementValue.value.length; i++) {
                    rule_group_str += `rizhi=${this.agrementValue.value[i]}&`
                }
                var url = "/#/business/search/searchLog";
                var params = `?&beginDate=${beginDate}&endDate=${endDate}&username=${val.emailName}&${rule_group_str}unitList=${this.unitValue.value}`;

                window.open(url + params, "_blank");
            },
            // 导出csv数据
            exportCsv() {
                this.exportLoading = true;
                if (this.searchData.length) {
                    // 配置表头
                    const field = this.columns
                        .map((item) => ({ label: item.title, value: item.dataIndex }))
                        .concat([]).filter(item => item.value!=='action');
                    let name = this.agrementValue.value.length>0?this.agrementValue.value.join('-'):'非法登录';
                    this.csvHelper.exports(
                        `${name}`,
                        this.searchData,
                        field
                    );
                    this.exportLoading = false;
                } else {
                    this.$message.warning("当前没有数据可以导出");
                    this.exportLoading = false;
                }
            },
            // 查询协议类型
            getAgreement(objId){
                this.$API.searchRuleGroup({objectId:objId}).then((res) => {
                    if (res.code === 1000) {
                    this.ruleGroupData = res.data;
                    } else {
                        this.ruleGroupData = [];
                    }
                });
            }
        },
        created(){
            this.objValue.value = this.objByCurrentUnit[0];
        },
        watch: {
            // 监听单位变化 提交vuex action接口 获取分析对象数据
            selectUnitEn:{
                handler:function(){
                    this.$store.dispatch('getObjByUnit',this);
                    this.getAnalysisObj();
                }
            },
        },
    }
</script>

<style scoped>
    .channelCls .ant-modal-content,.channelCls .ant-modal-header {
        background-color: #054273;
        color: #fff;
    }
</style>